export default function SettingsLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const pathname = usePathname()
  const [mobileMenuOpen, setMobileMenuOpen] = useState(false)

  // Check if we're on the main settings page
  const isMainSettingsPage = pathname === '/sub-account/settings'

  return (
    <div className="min-h-screen bg-gray-50">
      {/* Settings Header */}
      <div className="bg-white border-b border-gray-200 sticky top-0 z-20">
        <div className="px-4 sm:px-6">
          <div className="flex items-center justify-between h-14">
            <div className="flex items-center gap-3">
              <Link 
                href="/sub-account/dashboard"
                className="text-gray-500 hover:text-gray-700 flex items-center gap-2 text-sm"
              >
                <i className="fas fa-arrow-left text-xs"></i>
                <span className="hidden sm:inline">Back to Dashboard</span>
              </Link>
              <div className="h-5 w-px bg-gray-300 hidden sm:block"></div>
              <h1 className="text-lg font-semibold text-gray-900 flex items-center gap-2">
                <i className="fas fa-cog text-gray-400 text-sm"></i>
                Settings
              </h1>
            </div>
            
            {/* Mobile menu button */}
            <button
              onClick={() => setMobileMenuOpen(!mobileMenuOpen)}
              className="lg:hidden p-2 text-gray-500 hover:text-gray-700"
            >
              <i className={`fas ${mobileMenuOpen ? 'fa-times' : 'fa-bars'}`}></i>
            </button>
          </div>
        </div>
      </div>

      <div className="flex">
        {/* Settings Sidebar */}
        <aside className={`
          ${mobileMenuOpen ? 'block' : 'hidden'} lg:block
          w-full lg:w-56 bg-white border-r border-gray-200 min-h-[calc(100vh-3.5rem)]
          fixed lg:sticky top-14 left-0 z-10 lg:z-0
          overflow-y-auto flex-shrink-0
        `}>
          <nav className="py-3 px-2 space-y-4">
            {settingsMenu.map((section) => (
              <div key={section.section}>
                <h3 className="px-2 text-[10px] font-semibold text-gray-400 uppercase tracking-wider mb-1">
                  {section.section}
                </h3>
                <ul className="space-y-0.5">
                  {section.items.map((item) => {
                    const isActive = pathname === item.href
                    return (
                      <li key={item.href}>
                        <Link
                          href={item.href}
                          onClick={() => setMobileMenuOpen(false)}
                          className={`
                            flex items-center gap-2 px-2 py-1.5 rounded-md text-[13px] transition-all
                            ${isActive 
                              ? 'bg-red-50 text-red-700 font-medium' 
                              : 'text-gray-600 hover:bg-gray-50 hover:text-gray-900'
                            }
                          `}
                        >
                          <i className={`fas fa-${item.icon} w-4 text-center text-[11px] ${isActive ? 'text-red-600' : 'text-gray-400'}`}></i>
                          <span>{item.name}</span>
                        </Link>
                      </li>
                    )
                  })}
                </ul>
              </div>
            ))}
          </nav>
        </aside>

        {/* Main Content */}
        <main className="flex-1 min-h-[calc(100vh-3.5rem)] bg-gray-50 p-6">
          {isMainSettingsPage ? (
            // Show settings overview on main page
            <SettingsOverview />
          ) : (
            children
          )}
        </main>
      </div>

      {/* Mobile overlay */}
      {mobileMenuOpen && (
        <div 
          className="fixed inset-0 bg-black/50 z-0 lg:hidden"
          onClick={() => setMobileMenuOpen(false)}
        />
      )}
    </div>
  )
}

